@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";

.list-index {
 background:#0a2743 !important;
 padding: 20px 0;
 min-height: 100vh;
 
 .list__page{
	text-align: center;
	margin: 30px 0 0;
	list-style: none;
	padding: 0;

	li {
	 margin: 0 0 5px;
	 a {
		color:#fff;
		font-size: 25px;
		line-height: 30px;
	 }
	}
 }
 h1{
	margin: 0;
	color:#fff !important;
