body {

}
/* Start Home styles */
// ## Header ## 
header {
 @extend .d-block; 
}
.wrapper {
	@extend .d-block;
}
.container {
	margin: 0 auto;
	width: 1140px;
	display: block;
}
.main-nav {
	background: none !important;

	li {

		a {
			text-transform: none;
			font: 15px/76px $font-family-gotham;
			color: $color-grey;
			text-decoration: none;

			&:hover {
				font-family: $font-family-gotham-medium;
				color: $color-blue;
			}

			span {
				position: relative;
			}
		}

	}

	.uk-navbar-nav > li.uk-active > a {
		font-family: $font-family-gotham-medium;
		color: $color-blue;

		span {
			&::before {
				content: "";
				display: block;
				width: 100%;
				position: absolute;
				left: 0;
				top: 0;
				height: 5px;
				background: $color-red; 
			}
		}
	}
	.uk-navbar-nav > li > a {
		min-height: 1px;
		padding: 0 25px;
	}
	.uk-navbar-nav > li:first-child > a {
		padding-left: 0;
	}
}
.btn-request-call {
	color: $color-red !important;
	display: inline-block;
	font: 15px/15px $font-family-gotham;
	white-space: nowrap;
	border-bottom: 1px dotted $color-red;
	text-decoration: none !important; 

	&:hover {
		border-color: $color-white;
	}
}
.head-link-phone {
	font: 22px/22px $font-family-gotham;
	white-space: nowrap;
	color: $color-blue !important;
	margin-left: 20px;
}
.head-right-box {
	padding: 27px 0 0;
	width: 100%;
}
.head-right-call {
	margin-top: 3px;
}

/*PAGE HOME START*/
.content-page {
	@extend .d-block; 
}
.screen-one {
	@extend .d-block; 

 @media (min-width: 1200px) {
		background: url($img_dir+"screen-1.png") no-repeat top center;
