		background-size: cover;
 }	 
}
.logo {
	width: 162px;
}
.title-screen-one {
	font-size: 62px;
	line-height: 62px;
	letter-spacing: -1.4px;
	margin-top: 47px;
	margin-bottom: 34px;
}
.screen-one-content {
	display: block;
	width: 100%;
	padding: 33px 0 0;

	p {
		margin-bottom: 43px;
	}
}
.w-320 {
	width: 320px;
}
.btn-red {
	text-decoration: none !important;
	text-align: center;
	color: $color-white !important;
	 text-transform: uppercase;
	font: 13px/54px $font-family-gotham-medium;
	border-radius: 25.5px;
	display: inline-block;
	box-shadow: 0 11px 40px 0 rgba($color-red2, .3),  inset 0 -3px 0 0 rgba($color-black, .25);
	background: $color-red3;
	background: -moz-linear-gradient(top,  $color-red3 0%, $color-red4 50%, $color-red5 100%);
	background: -webkit-linear-gradient(top,  $color-red3 0%, $color-red4 50%,$color-red5 100%);
	background: linear-gradient(to bottom,  $color-red3 0%, $color-red4 50%,$color-red5 100%);
 // transition: box-shadow 0.3s;
 border: 0;
 padding: 0;
 position: relative;

	span {
		width: 100%;
		height: 100%;
		display: block;
		border-radius: 25.5px;
		box-shadow: inset 0 0 13px  rgba($color-red6, .86);
	}

	&:active {
		background: $color-red7;
		box-shadow: 0 0 0 9px rgba($color-red8, .34);

		&::before, &::after {
			opacity: 1;
		}
	}

	&::before {
		@extend .before;
		left: -9px;
		top: -9px;
		width: calc(100% + 18px);
		height: calc(100% + 18px);
		border-radius: 36px;
		box-shadow: 0 0 0 9px rgba($color-red8, .13);
		opacity: 0;
		transition: opacity 0.4s;
	}
	&::after {
		@extend .before;
		left: -18px;
		top: -18px;
		width: calc(100% + 36px);
		height: calc(100% + 36px);
		border-radius: 48px;
		box-shadow: 0 0 0 28px rgba($color-red8, .03);
		opacity: 0;
		transition: opacity 0.5s;		
	}
}

