@-webkit-keyframes animate-circle {
	from {
		-webkit-transform: scale(0);
		transform: scale(0);
		opacity: 1;
	}
	to {
		-webkit-transform: scale(1);
		transform: scale(1);
		opacity: 0;
	}
}

@keyframes animate-circle {
	from {
		-webkit-transform: scale(0);
		transform: scale(0);
		opacity: 1;
	}
	to {
		-webkit-transform: scale(1);
		transform: scale(1);
		opacity: 0;
	}
}


.equipment-loader {
	position: absolute;
	top: -70px;
	left: 50%;
	height: 1035px;
	width: 1035px;
	z-index: 5;
	margin-left: -517.5px;

 @media (max-width: 1149px) {
		height: 900px;
		width: 900px;
		margin-left: -450px;
		top: 0;
 }

 @media (max-width: 1023px) {
		height: 700px;
		width: 700px;
		margin-left: -350px;
		top: -80px;
 }		

	> .circle {
   --duration: 3s;
   
   position: absolute;
   height: inherit;
   width: inherit;
   border-radius: 50%;
   
   @media (min-width: 768px) {
   	animation: animate-circle var(--duration) cubic-bezier(.9,.24,.62,.79) infinite;
   }
   
   &:nth-of-type(3) {
     animation-delay: -3s;
     background: rgba($color-blue4, .6);
   }   
   &:nth-of-type(2) {
     width: 670px;
     height: 670px;
     background: rgba($color-blue4, .11);
     top: 50%;
     left: 50%;
     margin: -335px 0 0 -335px;
     animation-delay: -3s;

			 @media (max-width: 1149px) {
					height: 535px;
					width: 535px;
					margin: -267.5px 0 0 -267.5px;
			 }
			 @media (max-width: 1023px) {
					height: 335px;
					width: 335px;
					margin: -167.5px 0 0 -167.5px;
			 }		     
   }
   &:nth-of-type(1) {
     animation-delay: -3s;
     width: 450px;
     height: 450px;
     background: rgba($color-blue4, .42);
     top: 50%;
     left: 50%;
     margin: -225px 0 0 -225px;

				 @media (max-width: 1149px) {
						height: 315px;
						width: 315px;
						margin: -157.5px 0 0 -157.5px;
				 }		
				 @media (max-width: 1023px) {
						height: 115px;
						width: 115px;
						margin: -57.5px 0 0 -57.5px;
				 }		     
   }

	}
}

