.rev_slider {
	@media (min-width: 768px) {
  margin-left: -14%;
  margin-right: -14%;
  z-index: 5;
  margin-top: -80px;
  height: 630px;
	
	.slick-list {
	  padding-top: 200px !important;
	  padding-bottom: 130px !important;
	  padding-left: 240px !important;
	  padding-right: 240px !important;
	}

	.slick-dots {
	  text-align: right;
	  position: absolute;
	  bottom: 0;
	  left: 0;
	  right: 0;
	}

	.slick-track {
	  max-width: 100% !important;
	  transform: translate3d(0, 0, 0)!important;
	  perspective: 200px;
	}

	.slick-slide {
	  position: absolute;
	  top: 0;
	  left: 0;
	  right: 0;
	  bottom: 0;
	  margin: auto;
	  opacity: 0;
	  width: 100%!important;
	  transform: translate3d(0, 0, 0);
	  transition: transform 1s, opacity 1s;
	}

	.slick-snext,
	.slick-sprev {
	  display: block;
	}

	.slick-current {
	  opacity: 1;
	  position: relative;
	  display: block;
	  transform: translate3d(0, 0, -10px);
	  z-index: 2;

		.rev_slide {
			width: 660px !important;
		}	

		.review-box {
			padding: 20px 50px;
			
			&::before {
				opacity: 1;
			}
		}  
		

		.review-box-text {
			max-height: 155px;

			p {
					 font-size: 15px;
					 line-height: 26px;
			}
		}

		.review-user-pic {
			width: 46px;
			height: 46px;
			border-radius: 23px;
			margin-right: 16px;
		}		
		.review-author {
			margin-top: 28px;
		}
	} //.slick-current 

	.slick-snext {
	  opacity: 1;
	  transform: translate3d(29%, -11%, 0px);
	  z-index: 1;

	  .review-box {
	  	height: 255px;
	  	width: 500px;
	  }
	}

	.slick-sprev {
	  opacity: 1;
	  transform: translate3d(-27%, -30%, 0px);

	  .review-box {
	  	height: 255px;
	  	width: 500px;
	  }	  
	}
	.rev_slide {
		display: flex !important;
		justify-content: center;
	}

	.slick-slide {
		> div {
			width: 100%;
			display: flex !important;
			justify-content: center;
		}
	}

	} //-min-w 1024
} //end rev_slider

.reviews-home {
	@extend .d-block;
	overflow: hidden;
	margin-top: 92px;


	.h1 {
		font-size: 50px;
		line-height: 55px;
	}

	.container {
		position: relative;

	@media (min-width: 768px) {
			&::before, &::after {
				content: "";
				display: block;
				position: absolute;
				width: 267px;
				height: 262px;
				top: 195px;
				background-size: contain !important;
			}
