		box-shadow: 0 4px 12px 0 rgba(45, 78, 100, 0.13);
		display: flex;
		align-items: center;
		height: 60px;
		border-radius: 30px;
		justify-content: space-between;
	}
	.eq-text {
		font-size: 13px;
		line-height: 16px;
		padding-right: 10px;
	}
	.eq-img {
		max-width: 70px;
		min-width: 70px;

	 img {
			max-height: 100%;
		}
	}
	.equipment-content {
		padding-top: 0;
	}
	h2, .h2 {
		font-size: 20px;
		line-height: 24px;
	}
	.included-cost-box {
		font-size: 14px;
	}
	.included-cost-home h2 {
		text-align: center;
		margin-bottom: 20px;
	}
	.included-cost-home {
		margin-top: 30px;
	}
	.gallery-home {
		margin-top: 20px; 

		.gallery-link {
			width: 100%;
		}
		img {
			margin-left: auto;
			margin-right: auto;
		}
	}
	.cupon-home {
		background: url($img_dir+"bg-cupon-mob.jpg") no-repeat center;
		background-size: cover;
	}
	.cupon-content .h1 {
		font-size: 30px;
		line-height: 35px;
		margin-bottom: 20px;
		text-align: center;

		br {
			display: none;
		}
	}
	.cupon-content {
		width: 100%;
		padding: 20px 0;
	}
	.cupon-form {
		margin-top: 15px;
	}
	.cupon-content .btn {
		max-width: 320px;
		margin: 0 auto;
		display: block;
		float: none;
	}
	.reviews-home .h1 {
		font-size: 30px;
		line-height: 35px;
	}
	.review-box {
		box-shadow: 0 4px 20px 0 rgba(45, 78, 100, 0.13);
		height: auto;
		width: 100%;
		padding: 20px;
	}
	.review-box-text {
		max-height: none;
		-webkit-line-clamp: none;
		overflow: visible;
	}
	.rev_slide {
		overflow: hidden;
		padding: 20px;
	}
		.review-box-text {
			p {
					 font-size: 15px;
					 line-height: 26px;
			}
		}	
	.review-user-pic {
		width: 46px;
		height: 46px;
		border-radius: 23px;
		margin-right: 16px;
	}		
	.review-author {
		margin-top: 28px;
	}
	.rev_slider {
		padding-bottom: 70px;

		.slick-arrow {
			bottom: 20px;}
	}
	.triger-home {
		margin-top: 0;
		margin-bottom: 35px;
	}
	.medical-transportation-content .h1 {
		font-size: 30px;
		line-height: 35px;
		text-align: center;
	}
	.medical-transportation-content {
		padding: 30px 0;

		p {
			text-align: center;
		}
	}
	.medical-transportation-content .btn {
		display: block;
		margin-left: auto;
		margin-right: auto;
	}
	.medical-transportation-home {
		height: auto;
		background-size: cover;
		overflow: hidden;
	}
	.f-nav {
		display: block;
		width: 100%;

		li {
			display: block;
			width: 100%;
			padding: 0;

			a {
				display: block;
				width: 100%;
				padding: 5px 0;
				text-align: center;
			}
		}
	}
	.f-logo {
		margin: 0 auto;
	}
	.footer-right-box {
		padding-top: 0;
		text-align: center;
		margin-top: 20px;
	}
	.footer-right-box .head-right-call {
		float: none;
		margin-top: 15px;
	}
	footer {
		padding: 20px 0
	}
	body{ animation: -amp-start 8s steps(1, end) 0s 1 normal both; }
	
	.main-nav li a {
		line-height: 15px;
	}
	.main-nav .uk-navbar-nav > li.uk-active > a span::before {
		display: none;
	}
	.calc-label {
		height: auto;
	
		br {
			display: none;
		}
	}
	.calc-box {
		padding-bottom: 9px;
		height: auto;
	}
	.pb-xs-0 {
		padding-bottom: 0 !important;
	}
	.tarif-left-action {
		display: block;

		.d-flex, .calc-box {
			float: left;
			width: 100%;
		}
		.calc-box {
			margin-bottom: 20px;
		}
	}
	.tarif-left-action .btn {
		width: 100%;
		margin-left: 0;
	}
	.slide-discount-content {
		display: block;
	}
	.slide-discount-img {
		min-width: inherit;
		width: 100%;
		display: block;
		margin: 0 auto;
		overflow: hidden;
	}
	.slide-discount-box {
		width: 100%;
		padding: 0;
		float: left;
		margin-top: 30px;
	}
	.slider-nav-discount {
		top: inherit;
		bottom: 0;
		width: 100%;
		display: flex;
		justify-content: center;
	}
	.slider-discount {
		width: calc(100% + 20px);
		padding-bottom: 30px;
	}
	.slide-discount-container {
		padding: 0 10px;
	}
	.slide-discount-box {
		padding-bottom: 50px;
	}
	.slide-discount-text {
		font-size: 100px;
		line-height: 73px;		
	}
	.slide-discount-text span.percent {
		font-size: 73px;
		line-height: 52px;			
	}
	.discount-text-small {
		font-size: 90px;
		line-height: 67px;
	}
	.slide-discount-arrow {
		bottom: 2px;
	}
	.slide-discount-arrow .btn-prev-utp {
		left: calc(50% - 45px);
	}

	.slide-discount-arrow .btn-next-utp {
		right: calc(50% - 45px);
		left: inherit;
	}
	.page-title {
		font-size: 18px;
	}
	.tarif-container {
		overflow: hidden;
	}
	.tarif-calc-container {
		margin-bottom: 20px;
		margin-top: 40px;
	}
	.breadcrumb-nav {
		float: left;
	}
	.btn-top {
		margin-top: 0;
		margin-bottom: 10px;
	}
	.included-cost-reviews {
		margin-bottom: 50px;
	}
	.triger-car-col {
		margin-bottom: 20px;
		width: 50%;
	}
	.medical-transportation-partners {
		background: url($img_dir+"bg-partners-320.jpg") no-repeat center;
	}
	.partner-container .page-title {
		margin-bottom: 20px;
	}
	.medical-transportation-content p {
		br {
			display: none;
		}
	}
	.baloon-wrap {
		margin-left: 0 !important;
		border-radius: 160px;
		
		.si-content-wrapper {
			padding-top: 33px;
			padding-bottom: 33px;
			padding-left: 0;
			padding-right: 0;
			width: 320px;
			border-radius: 160px !important;
		}
		.si-pointer-top {
			display: none !important;
		}
		.si-content {
			max-width: 100% !important;
		}
	}
	.baloon-phone li a, .baloon-email li a, .baloon-adress li {
		font-size: 12px;
	}
	.baloon-sub-title {
		font-size: 14px;
		padding-left: 20px;
	}
	.baloon-phone {
		margin-bottom: 5px;
	}
	.baloon-email {
		margin-bottom: 10px;
	}
	.baloon-phone li a {
		padding-left: 20px;
	}
	.baloon-phone li a::before {
		width: 9px;
		height: 15px;
		top: 3px;
		background-size: cover;
	}
	.baloon-title {
		padding-left: 20px;
	}
	.baloon-email li a {
		padding-left: 20px;
	}
	.baloon-email li a::before {
		width: 13px;
		height: 10px;
		top: 3px;
		background-size: cover;
	}
	.baloon-adress li {
		padding-left: 20px;
	}
	.baloon-content {
		width: 200px;
	}
	.baloon-title {
		margin-bottom: 8px;
	}
}/* END #{$mediaWidth2} */	//-767px


