@media ($feature: $mediaWidth3) {
	.review-box {
		box-shadow: 0 3px 8px 0 rgba(45, 78, 100, 0.2);
		padding: 15px;
	}
	.review-box-text p {
		font-size: 14px;
		line-height: 22px;
	}
	.review-author {
		margin-top: 20px;
	}
	.rev_slide {
		padding: 20px 5px;
	}
	.triger-car-col {
		width: 100%;
	}

}/* END #{$mediaWidth3} */	//-479px

